<template>
    <section>
        <a-back-top />
        <!-- <div :class="[{'preloader':true,'active':active}]">
            <div ref="num" :class="[{'count':true,'hide':hide}]">0</div>
        </div>-->

        <div class="back">
            <div @click="goBack()" class="back-button">
                <a-icon type="left" />&nbsp;首页
            </div>
        </div>
        <div class="main-text">
            <div class="waterfall no-scroll">
                <div
                    class="card"
                    @click="checkItem(item)"
                    v-for="(item,index) in list"
                    :key="index"
                >
                    <img class="item-img" :src="'../../../static/imgs/items/item-'+index+'.jpg'" />
                    <div class="card-content">
                        <span class="title">{{item.title}}</span>
                        <span class="desc">{{item.desc}}</span>
                    </div>
                </div>
                <!-- <a-row type="flex" justify="center" align="top" :gutter="[16,16]">
                    <a-col :span="8" v-for="(item,index) in list" :key="index">
                        <div class="card" @click="checkItem(item)">
                            <img class="item-img" :src="item.img" />
                            <div class="card-content">
                                <p>{{item.title}}</p>
                                <p>{{item.desc}}</p>
                            </div>
                        </div>
                    </a-col>
                </a-row>-->
            </div>
        </div>
    </section>
</template>
<script>
import { list } from "./config/list";
export default {
    data () {
        return {
            time: 3000,
            hide: false,
            active: true,
            list,
        };
    },
    components: {

    },

    mounted () {
        this.active = this.$route.params.active != undefined ? this.$route.params.active : true;
        this.$nextTick(() => {
            setTimeout(() => this.init(), 600)
            // this.init()
        })
    },
    activated () {
        console.log(222)
    },
    deactivated () {
        console.log('fake-finish')
    },
    watch: {

    },
    methods: {
        addOne () {
            this.$store.commit("addOne", 1);
        },
        async () {
            this.$store.dispatch("addOneAsync", 2);
        },
        toCard () {
            this.$router.push("/colorfulLife/cardHover");
        },
        load () {
            //预加载界面
            // let numDiv = this.$refs.num;
            // let counter = setInterval(() => {
            //     let num = parseInt(numDiv.textContent);
            //     numDiv.textContent = (++num).toString();
            //     if (num == 100) {
            //         clearInterval(counter);
            //         this.hide = true;
            //         this.active = true;
            //     }
            // }, 30);
        },
        checkItem (item) {
            if (item.url) {
                this.$router.push(item.url);

            } else {
                this.$router.push({ name: item.name, params: item.params })
            }
        },
        goBack () {
            this.$router.push('/');
        },
        init () {
            //   console.log($(".item").outerWidth(true),document.getElementsByClassName("item")[0].offsetWidth);
            // console.log($(window).width() ,window.innerWidth)

            // offsetWidth 不包括外边距   jQuery outerWidth()  传参true-> 包括外边距
            // 使用window.innerWidth / 列宽+ margin

            // let nodeWidth = $('.card').outerWidth(true),
            // let colNum = 4,
            //     nodeWidth = parseInt((window.innerWidth - 100 - (colNum - 1) * 10) / colNum),
            let nodeWidth = 400,
                colNum = parseInt((window.innerWidth) / nodeWidth),
                midWidth = parseInt((window.innerWidth - colNum * nodeWidth) / (colNum + 1)),
                midHeight = 20,
                colSumHeight = []

            for (let i = 0; i < colNum; i++) {
                colSumHeight.push(0)
            }

            document.querySelectorAll('.card').forEach((item, index) => {
                let $cur = item,
                    idx = 0,
                    minSumHeight = colSumHeight[0]


                // 获取到solSumHeight中的最小高度
                for (let i = 0; i < colSumHeight.length; i++) {
                    if (minSumHeight > colSumHeight[i]) {
                        minSumHeight = colSumHeight[i]
                        idx = i
                    }
                }
                // 设置各个item的css属性
                $cur.style.width = nodeWidth + 'px'
                $cur.style.left = idx === 0 ? midWidth + 'px' : ((nodeWidth + midWidth) * idx) + midWidth + 'px'
                $cur.style.top = (minSumHeight + parseInt(index / colNum) * midHeight) + 'px'
                // console.log(index, minSumHeight, $cur.style.top, $cur.offsetHeight)


                // 更新solSumHeight
                colSumHeight[idx] = colSumHeight[idx] + $cur.offsetHeight
            })


            // 设置窗口改变时也能重新加载
            window.addEventListener('resize', this.throttle(this.init, 1000))
        },
        throttle (func, delay) {
            let prev = 0
            return () => {
                let now = Date.now()
                if (now - prev > delay) {
                    func.call(this)
                    prev = now
                }
            }
        }
    }
};
</script>
<style scoped lang="scss">
section,
div {
    box-sizing: border-box;
}
section {
    width: 100vw;
    height: 100vh;
    background-color: #02161e;
    overflow: scroll;
    .back-button {
        padding: 5px 8px;
        cursor: pointer;
        color: #fff;
        font-weight: 500;
        outline: none;
        border: none;
        font-size: 18px;
    }

    .back {
        z-index: 10;
        position: fixed;
        padding: 2rem 0 0 6rem;
        text-align: left;
    }

    .no-scroll {
        -ms-overflow-style: none; // IE 10+
        overflow: -moz-scrollbars-none; // Firefox

        &::-webkit-scrollbar {
            // chrome
            display: none;
        }
    }

    .main-text {
        width: 100%;
        height: 100%;
        padding-top: 6rem;
        padding-bottom: 3rem;
        display: flex;
        justify-content: center;
        .waterfall {
            width: 100%;
            height: calc(100vh - 9rem);
            box-sizing: border-box;
            overflow: scroll;
            position: relative;
            .card {
                // border-radius: 8px;
                background-color: #fff;
                position: absolute;
                transition: all 1s;
                box-sizing: border-box;
                animation: show 1.3s ease 1;
                .item-img {
                    // border-radius: 10px 10px 0px 0px;
                    width: 100%;
                    height: auto;
                }
                .card-content {
                    padding: 0.5rem;
                    box-sizing: border-box;
                    .title {
                        display: block;
                        font-size: 18px;
                        font-weight: 600;
                        // font-family: 'Microsoft YaHei';
                        text-align: start;
                    }
                    .desc {
                        display: block;
                        margin-top: 0.5rem;
                        text-align: start;
                        color: #7f8c8d;
                    }
                }
            }

            @keyframes show {
                0% {
                    opacity: 0;
                }
                80% {
                    opacity: 0;
                }
                100% {
                    opacity: 1;
                }
            }
        }
    }
}
</style>


